@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1A1A1A;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.gameTitle {
  font-size: 48px;
  text-align: center;
}

.sectionTitle {
  font-size: 32px;
  text-align: center;
}

.gutter {
  width: 100%;
  padding: 16px;
}

@media only screen and (min-width: 768px) {
  .gutter {
    max-width: 1000px;
    margin: 0 auto;
  }
}

.gameBorder {
  padding: 16px;
  display: flex;
  flex-direction: column;
  height: 100vh;
  max-width: 1000px;
  margin: 0 auto;
  overflow: hidden;
}

.buttonMenu {
  display: flex;
  flex-direction: row;
}

.buttonMenu > button:not(:first-of-type) {
  margin-left: 16px;
}

.gameBoard {
  height: 80%;
  margin-bottom: 16px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: repeat(5, 1fr);
  grid-gap: 16px;
  gap: 16px;
  grid-template-areas:
    ". . . ."
    ". . . ."
    ". . . ."
    ". . . ."
    ". . . ."
}

.leaderBoard {
  height: 10%;
  margin-top: auto;
  margin-bottom: 16px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 16px;
  gap: 16px;
  grid-template-areas:
    "left right"
}

#playerOne {
  grid-area: left;
  text-align: left;
}

#playerTwo {
  grid-area: right;
  text-align: right;
}

.gameMenu {
  height: 10%;
}



.scene {
  width: 100%;
  height: 100%;
  perspective: 600px;
  justify-self: center;
}

.card {
  width: 100%;
  height: 100%;
  position: relative;
  transition: transform 1s;
  transform-style: preserve-3d;
}

.card__face {
  border-radius: 8px;
  position: absolute;
  height: 100%;
  width: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, .25);
}

.card__face--front {
  background-image: linear-gradient(45deg, rgb(255, 174, 0), rgb(255, 60, 0));
  cursor: pointer;
}

.card__face--back {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transform: rotateY( 180deg );
}

.card.is-flipped {
  transform: rotateY(180deg);
}

.card.fly-left {
  z-index: 5;
  transform: translate3d(-750px, 1000px, 50px);
}

.card.fly-right {
  z-index: 5;
  transform: translate3d(750px, 1000px, 50px);
}

