button {
  width: 100%;
  color: black;
  font-weight: 700;
  font-size: 16px;
  padding: 12px 16px;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 0px 15px 0px rgb(0 0 0 / 30%);
  cursor: pointer;
  background-color: white;
}

.player {
  border-radius: 15px;
  padding: 16px;
  background-color: #808080;
}

.player.active {
  background-color: #ffffff;
}

.player.active > .playerName {
  font-weight: 700;
}

.playerName {
  font-size: 12px;
  font-weight: 300;
  margin: 0;
}

.score {
  font-size: 16px;
  font-weight: 700;
  margin: 0;
}

@media only screen and (min-width: 768px) {
  .playerName {
    font-size: 16px;
  }
  .score {
    font-size: 24px;
  }
}
.startMenu {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0, .8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.gameStartContent {
  padding: 32px;
}

.gameTitle {
  color: white;
  font-size: 32px;
}
.instructions {
  padding: 16px;
  text-align: center;
  color: black;
  background: white;
  border-radius: 5px;
}

#gameInstructions {
  position: absolute;
}

.gameOver {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0, .8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.gameOverContent {
  color: white;
  text-align: center;
}

.gameOverContent > h1 {
  margin: 0;
  font-size: 72px;
}

.gameOverContent > p {
  margin: 0;
  margin-bottom: 32px;
  font-size: 48px;
}
